.main-header__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @media (min-width: $tablet-width) {
        justify-content: space-between;
    }

    @media (min-width: $desktop-width) {
        justify-content: space-between;
    }
}

.main-header__logo-wrapper {
    @media (min-width: $desktop-width) {
        display: flex;
        justify-content: space-between;
        margin-right: 20px;
    }
}

.main-header__logo-mobile {
    @media (min-width: $tablet-width) {
        display: none;
    }
}

.main-header__logo-tablet {
    display:none;

    @media (min-width: $tablet-width) {
        display: block;
    }

    @media (min-width: $desktop-width) {
        display: none;
    }
}

.main-header__logo-desktop {
    display:none;

    @media (min-width: $desktop-width) {
        display: block;
        margin-right: 20px;
    }
}

.main-header__logo-description {
    display: none;

    @media (min-width: $desktop-width) {
        display: block;
        font-size: 12px;
        line-height: 16px;
    }
}