.map {
    margin-bottom: 22px;

    @media (min-width: $tablet-width) {
        margin-bottom: 45px;
    }

    @media (min-width: $desktop-width) {
        margin-bottom: 50px;
    }
}

.map__container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.map__image {
    width: 100%;
    object-fit: cover;
    background-image: url("../images/map-mobile.jpg");
    background-repeat: no-repeat;

    @media (min-resolution: 2dppx) {
        background-image: url("../images/map-mobile@2x.jpg");
    }

    @media (min-width: $tablet-width) {
        background-image: url("../images/map-tablet.jpg");

        @media (min-resolution: 2dppx) {
            background-image: url("../images/map-tablet@2x.jpg");
        }
    }

    @media (min-width: $desktop-width) {
        background-image: url("../images/map-desktop.jpg");

        @media (min-resolution: 2dppx) {
            background-image: url("../images/map-desktop@2x.jpg");
        }
    }
}

//    ID     JS
#map {
    height: 420px;

    @media (min-width: $tablet-width) {
        height: 480px;
    }

    @media (min-width: $desktop-width) {
        height: 540px;
    }
}