.socials {
    position: relative;
    padding-bottom: 22px;
    margin-bottom: 19px;

    &::after {
        position: absolute;
        content: "";
        width: 100%;
        height: 1px;
        background-color: #f0ebff;
        bottom: 0;

        @media (min-width: $tablet-width) {
            display: none;
        }
    }

    @media (min-width: $tablet-width) {
        grid-column: 1 / -1;
        padding-bottom: 8px;
    }

    @media (min-width: $desktop-width) {
        margin-top: -10px;
        grid-column: 1 / 2;
    }
}

.socials__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @media (min-width: $desktop-width) {
        flex-direction: column;
        justify-content: space-between;
        width: auto;
    }
}

.socials__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style-type: none;

    @media (min-width: $desktop-width) {
        margin-top: 50px;
    }
}


.socials__item {
    margin-right: 20px;
}

.socials__item:last-child {
    margin-right: 0;
}

.socials__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;

    &:hover,
    &:focus {
        outline: none;
        background-color: #f0ebff;
    }

    &:active {
        outline: none;
        background-color: rgba(120, 89, 207, 0.25);
    }
}

.socials__link svg {
    fill: #bdbdbd;

    &:hover,
    &:focus {
        fill: #7859cf;
    }
}

