.navigation {
    width: 100%;
    @media (min-width: $tablet-width) {
        order: 2;
        width: auto;
    }

    @media (min-width: $desktop-width) {
        width: 778px;
        display: flex;
        justify-content: center;
    }
}

.navigation__list {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
    list-style-type: none;
    display: none;

    @media (min-width: $tablet-width) {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: space-between;
        max-width: 470px;
        padding-left: 44px;
    }

    @media (min-width: $desktop-width) {
        max-width: 740px;
    }
}

.navigation__item {
    position: relative;
    width: 100%;
    text-align: center;
    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    text-transform: uppercase;
    background-color: #ffffff;

    &::after {
        position: absolute;
        content: "";
        width: calc((100vw - (100vw - 280px)/2*-1));
        height: 1px;
        left: calc((100vw - 280px)/2*-1);
        bottom: 0;
        background-color: #f0ebff;

        @media (min-width: $tablet-width) {
            display: none;
        }
    }

    @media (min-width: $tablet-width) {
        width: auto;
        position: relative;
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        text-transform: uppercase;
    }

    @media (min-width: $desktop-width) {
        margin-right: 20px;
    }
}

.navigation__item:first-child {
    &::before {
        position: absolute;
        content: "";
        width: calc((100vw - (100vw - 280px)/2*-1));
        height: 1px;
        left: calc((100vw - 280px)/2*-1);;
        top: 0;
        background-color: #f0ebff;

        @media (min-width: $tablet-width) {
            display: none;
        }
    }
}

.navigation__link {
    display: block;
    padding: 20px 0 21px 0;
    text-decoration: none;
    color: #333333;
    cursor: pointer;

    &:hover,
    &:focus {
        color: #7859cf;
    }

    @media (min-width: $tablet-width) {
        padding: 32px 20px;
    }
}

.navigation__link-current  {
    cursor: pointer;

    &:active {
        background-color: #f0ebff;
    }

    @media (min-width: $tablet-width) {
        color: #7859cf;

        &:hover,
        &:focus {
            &::after {
                display: none;
            }
        }

        &:active {
            background-color: #f0ebff;
        }

        &::after {
            position: absolute;
            content: "";
            width: 100%;
            height: 2px;
            right: 0;
            bottom: 0;
            background-color: #7859cf;
        }
    }
}

.navigation__toggle {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: transparent;
    border: none;
    width: 60px;
    height: 60px;
    margin-right: -20px;

    &::after {
        display: block;
        content: "";
        width: 16px;
        height: 2px;
        margin-top: 4px;
        background-color:rgba(51, 51, 51, 1);
    }

    &::before {
        display: block;
        content: "";
        width: 16px;
        height: 2px;
        margin-bottom: 4px;
        background-color:rgba(51, 51, 51, 1);
    }

    &:active {
        background-color: #f0ebff;
    }

    @media (min-width: $tablet-width) {
        display: none;
    }
}

.navigation__toggle-line {
    display: block;
    width: 16px;
    height: 2px;
    background-color:rgba(51, 51, 51, 1);
    border: none;

    @media (min-width: $tablet-width) {
        display: none;
    }
}

//     js

.navigation-no-js .navigation__list {
    display: block;

    @media (min-width: $tablet-width) {
        display: flex;
    }
}

.navigation-no-js .navigation__toggle {
    display: none;
}

//    js

.navigation__nav--closed .navigation__toggle {
    display: flex;

    @media (min-width: $tablet-width) {
        display: none;
    }
}

.navigation__nav--closed,
.navigation__nav--opened {
    width: auto;
}

.navigation__nav--opened .navigation__list {
    display: block;
    position: absolute;
    width: 100%;
    z-index: 20;
    right: 0;
    top: 64px;

    @media (min-width: $tablet-width) {
        display: flex;
        position: static;
    }
}

.navigation__nav--opened .navigation__toggle {
    &::after {
        transform: translateY(-6px) rotate(135deg);
    }

    &::before {
        transform: translateY(6px) rotate(45deg);
    }

    @media (min-width: $tablet-width) {
        display: none;
    }
}

.navigation__nav--opened .navigation__toggle-line {
    background-color: transparent;
}