.promo {
    background-color: #f3ebe1;

    @media (min-width: $tablet-width) {
        background: linear-gradient(to bottom, #f3ebe1 76%, #ffffff 76%);

    }

    @media (min-width: $desktop-width) {
        margin-bottom: 50px;
        background: linear-gradient(to bottom, #f3ebe1 70% , #f3ebe1 70%);
    }
}

.promo__image-wrapper {
    margin-bottom: 5px;
    @media (min-width: $tablet-width) {
        margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
        margin-left: 66px;
        margin-right: 30px;
    }
}

.promo__image {
    @media (min-width: $tablet-width) {
        width: 476px;
        height: 476px;
        margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
        width: 600px;
        height: 600px;
    }
}

.promo__content-wrapper {
    @media (min-width: $tablet-width) {
        order: -1;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    @media (min-width: $desktop-width) {
        order: 1;
        align-items: start;
        justify-content: center;
    }
}

.promo__header {
    margin-bottom: 4px;
    text-align: center;

    @media (min-width: $desktop-width) {
        align-self: start;
        margin-bottom: 4px;
    }
}

.promo__name {
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    font-size: 36px;
    line-height: 48px;
    font-weight: 700;
    letter-spacing: 1.2px;
    text-align: center;

    @media (min-width: $tablet-width) {
        font-size: 56px;
        line-height: 80px;
        margin-bottom: 15px;
    }

    @media (min-width: $desktop-width) {
        margin-bottom: 16px;
    }
}

.promo__description {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    text-align: center;

    @media (min-width: $tablet-width) {
        padding: 0;
        max-width: 608px;
        font-size: 20px;
        line-height: 32px;
    }

    @media (min-width: $desktop-width) {
        grid-column: 2 / 3;
        text-align: start;
        margin-bottom: 40px;
    }
}

.promo__wrapper {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 60px;
    align-items: center;

    @media (min-width: $tablet-width) {
        margin-bottom: 20px;
    }

    @media (min-width: $desktop-width) {
        grid-column: 2 / 3;
        margin-bottom: 12px;
    }
}

.promo__old-price {
    font-size: 16px;
    line-height: 32px;
    color: #7859cf;
    text-decoration: line-through;

    @media (min-width: $tablet-width) {
        margin-right: 10px;
    }
}

.promo__new-price {
    font-size: 20px;
    line-height: 32px;

    @media (min-width: $tablet-width) {
        margin-right: 20px;
    }
}

.promo__button {
    padding: 8px 20px;
    font-size: 16px;
    line-height: 24px;
    color: #ffffff;
}
