.catalog {
    background-color: #f8f8f8;
    padding: 50px 0 44px 0;
    text-align: center;

    @media (min-width: $tablet-width) {
        padding-top: 83px;
        padding-bottom: 90px;
    }

    @media (min-width: $desktop-width) {
        padding-top: 100px;
        padding-bottom: 120px;
    }
}

.catalog__container {
    @media (min-width: $desktop-width) {
        display: grid;
        grid-template-columns: 318px auto;
        column-gap: 30px;
    }
}

.catalog__wrapper {
    margin-bottom: 45px;

    @media (min-width: $tablet-width) {
        margin-bottom: 40px;
    }
    
    @media (min-width: $desktop-width) {
        grid-column: 2 / 3;
        grid-row: 4 / 5;
        margin-bottom: 30px;
    }
}

.catalog__header {
    margin-bottom: 5px;

    @media (min-width: $desktop-width) {
        grid-column: 1 / -1;
        grid-row: 1 / 2;
    }
}

.catalog__header-description {
    margin: 0;
    margin-bottom: 20px;
    padding: 0;
    font-size: 24px;
    line-height: 36px;
    font-weight: 700;

    @media (min-width: $tablet-width) {
        font-size: 42px;
        line-height: 64px;
        margin-bottom: 20px;
    }

    @media (min-width: $desktop-width) {
        grid-column: 1 / -1;
        grid-row: 2 / 3;
        margin-bottom: 38px;
    }
}

.catalog__sorting-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;

    @media (min-width: $tablet-width) {
        justify-content: flex-end;
        margin-bottom: 26px;
    }

    @media (min-width: $desktop-width) {
        grid-column: 2 / 3;
        grid-row: 3 / 4;
    }
}

.catalog__sorting-title {
    margin: 0;
    margin-right: 13px;
    padding: 0;
    font-size: 16px;
    line-height: 24px;
    font-weight: 600;

    @media (min-width: $tablet-width) {
        margin-right: 0;
    }
}

.catalog__sorting-control {
    position: relative;
    display: flex;
    width: auto;
    border: none;
    outline: none;
    font-size: 16px;
    font-family: $basic-font;
    line-height: 24px;
    font-weight: 600;
    border-radius: 4px;
}

.catalog__sorting-control[data-state="active"] .catalog__sorting-control-content {
    opacity: 1;
}
  
.catalog__sorting-control-title {
    width: 190px;  
    padding: 4px 22px 4px 10px;
    color: #7859cf;
    background-color: transparent;
    background-image: url("../images/select-arrow-down.svg"); 
    background-repeat: no-repeat, repeat;
    background-position: right 0.6em top 50%, 0 0;
    background-size: .65em auto, 100%; 

    &:hover,
    &:focus {
        background-color: #f0ebff;
    }

    &:active {
        background-color: transparent;
        background-image: url("../images/select-arrow-up.svg");
    }

    @media (min-width: $tablet-width) {
        width: 180px;
        padding: 4px 22px 4px 0;
    }
}
  
.catalog__sorting-item {
    display: none;
}
  
.catalog__sorting-control-content {
    position: absolute;
    top: 32px;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 197px; 
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    background-color: #ffffff;
    border-radius: 4px;
    opacity: 0;
}
  
.catalog__sorting-control-label {
    padding: 8px 28px 8px 20px;
    border-bottom: 1px solid ;
    font-weight: 400;
    text-align: center;
}

.catalog__sorting-item:checked + .catalog__sorting-control-label {
    color: #ffffff;
    background-color: #7859cf;
    border-radius: 4px;
}
  
.catalog__sorting-item:hover + .catalog__sorting-control-label,
.catalog__sorting-item:focus + .catalog__sorting-control-label {
    background-color: #f0ebff;
    border-radius: 4px;
}
  
.catalog__list {
    margin: 0;
    padding: 0;
    list-style-type: none;

    @media (min-width: $tablet-width) {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }
}

.catalog__card-wrapper {
    display: flex;
    flex-direction: column;
    margin-bottom: 22px;
    padding: 40px 20px 20px 20px;
    background-color: #ffffff;

    @media (min-width: $tablet-width) {
        max-width: 349px;
        margin-right: 30px;
        margin-bottom: 30px;
        padding: 50px 30px 30px 30px;
    }

    @media (min-width: $desktop-width) {
        max-width: 317px;

        &:hover,
        &:focus {
            box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
        }
    }
}

.catalog__card-wrapper:last-child {
    margin-bottom: 0;

    @media (min-width: $tablet-width) {
        margin-bottom: 30px;
    }
}

.catalog__card-wrapper:nth-child(2n) {
    @media (min-width: $tablet-width) {
        margin-right: 0;
    }

    @media (min-width: $desktop-width) {
        margin-right: 30px;
    }
}

.catalog__card-wrapper:nth-child(3n) {
    @media (min-width: $desktop-width) {
        margin-right: 0;
    }
}

.catalog__image-link {
    margin-bottom: 19px;

    @media (min-width: $tablet-width) {
        margin-bottom: 25px;
    }
}

.catalog__card-title {
    padding: 0;
    margin: 0;
    margin-bottom: 4px;
    font-size: 18px;
    line-height: 32px;
    font-weight: 700;
    text-align: left;
    color: #333333;

    @media (min-width: $tablet-width) {
        font-size: 24px;
        line-height: 36px;
        margin-bottom: 9px;
    }
}

.catalog__title-link {
    font-family: inherit;
    text-decoration: none;
}

.catalog__card-description {
    margin: 0;
    padding: 0;
    padding-right: 10px;
    margin-bottom: 16px;
    text-align: left;

    @media (min-width: $tablet-width) {
        margin-bottom: 22px;
    }
}

.catalog__card-inner-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.catalog__price {
    font-size: 20px;
    line-height: 32px;
    font-weight: 600;
}

.catalog__button {
    position: relative;
    padding: 8px 24px 10px 45px;
    font-family: inherit;

    &::before {
        position: absolute;
        content: "";
        width: 20px;
        height: 16px;
        top: 50%;
        transform: translateY(-8px);
        left: 16px;
        background-image: url("../images/button-icon.svg");
    }
}