.filters {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    @media (min-width: $tablet-width) {
        flex-direction: row;
        margin-bottom: 40px;
    }

    @media (min-width: $desktop-width) {
        grid-column: 1 / 2;
        grid-row: 3 / 5;
        flex-direction: column;
        justify-content: flex-start;
    }
}

.filters__price,
.filters__milk,
.filters__country {
    margin: 0;
    padding: 0;
    border: none;

    @media (min-width: $tablet-width) {
        margin-bottom: 5px;
    }
}

.filters__title {
    font-size: 16px;
    line-height: 24px;
    font-weight: 600;
    text-align: start;
    margin-bottom: 32px;
}

.filters__title-price {
    @media (min-width: $tablet-width) {
        margin-bottom: 29px;
    }
}

.filters__title-milk {
    margin-bottom: 22px;

    @media (min-width: $tablet-width) {
        margin-bottom: 20px;
    }
}

.filters__title-country {
    margin-bottom: 20px;
}

.filters__price {
    margin-bottom: 24px;

    @media (min-width: $tablet-width) {
        min-width: 220px;
        margin-right: 20px;
    }

    @media (min-width: $desktop-width) {
        margin-right: 0;
        margin-bottom: 31px;
    }
}

.filters__milk {
    @media (min-width: $tablet-width) {
        margin-right: 38px;
        min-width: 210px;
    }

    
    @media (min-width: $desktop-width) {
        margin-bottom: 10px;
    }
}

.filters__milk-list,
.filters__country-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    text-align: left;
}

.filters__country-list {
    margin-bottom: 34px;
}

.filters__milk-item {
    margin-bottom: 22px;
    padding-left: 24px;

    @media (min-width: $tablet-width) {
        margin-bottom: 20px;
    }
}

.filters__country-item {
    margin-bottom: 20px;
    padding-left: 44px;
}

.filters__country-item:last-child {
    margin-bottom: 0;
}

.filters__milk-item:last-child,
.filters__country-item:last-child {
    color: #bdbdbd;
}

.filters__button-wrapper {    
    @media (min-width: $desktop-width) {
        display: flex;
        flex-wrap: wrap;
    }
}

.filters__button-submit {
    align-self: center;
    font-size: 13px;
    line-height: 24px;
    padding: 11px 14px;
    margin-bottom: 24px;

    @media (min-width: $tablet-width) {
        font-size: 14px;
        padding: 8px 3px;
        margin-left: 181px;
        margin-right: 30px;
        margin-bottom: 0;
    }

    @media (min-width: $desktop-width) {
        margin-left: 0;
        margin-right: 25px;
    }
}

.filters__button-reset {
    align-self: center;
    font-size: 13px;
    line-height: 24px;
    padding: 11px 14px;
    color: #7859cf;
    background-color: #ffffff;
    margin-bottom: 36px;

    @media (min-width: $tablet-width) {
        font-size: 14px;
        padding: 8px 5px;
        margin-bottom: 0;
    }

    &:hover,
    &:focus,
    &:active,
    &:disabled {
        color: #ffffff;
    }

    &:disabled {
        background-color: #e2e2e2;
    }
}