.footer__container {
    @media (min-width: $tablet-width) {
        display: grid;
        grid-template-columns: repeat(3, 223px);
        column-gap: 30px;
        justify-content: center;
    }

    @media (min-width: $desktop-width) {
        grid-template-columns: repeat(4, 318px);
    }
}

.footer__logo {
    @media (min-width: $tablet-width) {
        display: none;
    }
}

.footer__logo-text {
    display: none;

    @media (min-width: $desktop-width) {
        display: block;
    }
}

.footer__logo-tablet {
    display: none;

    @media (min-width: $tablet-width) {
        display: block;
    }

    @media (min-width: $desktop-width) {
        margin-bottom: 10px;;
    }
}

.footer__title {
    margin: 0;
    margin-bottom: 20px;
    padding: 0;
    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    color: #7859cf;
}

.footer__navigation-list,
.footer__media-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.footer__navigation-item,
.footer__media-item {
    margin-bottom: 20px;
    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    text-transform: uppercase;
}

.footer__navigation-link,
.footer__media-link {
    font-family: inherit;
    text-decoration: none;
    color: #333333;

    &:hover,
    &:focus {
        color: #9070ec;
    }

    &:active {
        color: #593cac;
    }
}

.footer__shop,
.footer__media {
    margin-bottom: 19px;
    padding-bottom: 10px;
}

.footer__shop,
.footer__media,
.footer__contacts {
    position: relative;

    &::after {
        position: absolute;
        content: "";
        width: 100%;
        height: 1px;
        background-color: #f0ebff;
        bottom: 0;

        @media (min-width: $tablet-width) {
            display: none;
        }
    }

    @media (min-width: $tablet-width) {
        margin-bottom: 0;
    }

    @media (min-width: $desktop-width) {
        padding-bottom: 0;
    }
}

.footer__contacts {
    padding-bottom: 22px;
    margin-bottom: 20px;

    @media (min-width: $desktop-width) {
        margin-bottom: 0;
        padding-bottom: 0;
    }
}

.footer__contacts-wrapper {
    @media (min-width: $tablet-width) {
        width: auto;
    }
}

.footer__address-text {
    position: relative;
    padding-left: 20px;
    font-style: normal;

    &::before {
        position: absolute;
        content: "";
        width: 13px;
        height: 16px;
        top: 0;
        left: 0;
        background-image: url("../images/address.svg");
    }
}

.footer__address-phone {
    position: relative;
    padding-left: 20px;
    text-decoration: none;
    font-style: normal;
    color: #333333;

    &::before {
        position: absolute;
        content: "";
        width: 13px;
        height: 16px;
        top: 0;
        left: 0;
        background-image: url("../images/phone.svg");
    }
}

.footer__copyright-text {
    font-size: 12px;
    line-height: 16px;
    color: #bdbdbd;
    text-align: center;
    vertical-align: middle;
}

.footer__copyright-link {
    font-family: inherit;
    text-decoration: none;
    color: #bdbdbd;
}

.footer__copyright-link:hover svg,
.footer__copyright-link:focus svg {
    outline: transparent;
    fill: #7859cf;
}

.footer__copyright-link:hover,
.footer__copyright-link:focus {
    outline: transparent;
    color: #7859cf;
}

.footer__copyright-link:active svg {
    outline: transparent;
    fill: #593cac;
}

.footer__copyright-link:active {
    outline: transparent;
    color: #593cac;
}

.footer__copyright-link svg {
    display: inline-block;
    vertical-align: bottom;
    fill: #bdbdbd;
}