
.swiper {
    max-width: 320px;
    height: auto;

    @media (min-width: $tablet-width) {
        padding-top: 40px;
        max-width: $tablet-width;
    }

    @media (min-width: $desktop-width) {
        max-width: $desktop-width;
        padding-top: 10px;
        padding-bottom: 75px;
    }
}
  
.swiper-card-wrapper {
    padding-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @media (min-width: $desktop-width) {
        flex-direction: row;
        justify-content: start;
    }
}

.swiper-button-prev,
.swiper-button-next {
    width: 80px;
    height: 100%;
    top: -76px;
}

.swiper-button-prev {
    left: -34px;
    &:hover,
    &:focus{
        background: linear-gradient(to right, #ffffff 0%,rgba(255, 255, 255, 0) 100%);
    }

    &:active {
        background: linear-gradient(to right, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
    }

    &:disabled {
        background: linear-gradient(to right, transparent 0%, transparent 100%);
    }

    @media (min-width: $tablet-width) {
        left: -20px;
    }

    @media (min-width: $desktop-width) {
        left: -33px;
        top: 23px;
    }
}

.swiper-button-next {
    right: -34px;
    &:hover,
    &:focus{
        background: linear-gradient(to left, ffffff 0%,rgba(255, 255, 255, 0) 100%);
    }

    &:active {
        background: linear-gradient(to left, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
    }

    &:disabled {
        background: linear-gradient(to left, transparent 0%, transparent 100%);
    }

    @media (min-width: $tablet-width) {
        right: -20px;
    }

    @media (min-width: $desktop-width) {
        right: -33px;
        top: 23px;
    }
}

.swiper-button-prev::after,
.swiper-button-next::after {
    color: #7859cf;
    font-size: 18px;
    font-weight: 700;
}

.swiper-pagination {
    display: flex;
    justify-content: center;
}

.swiper-horizontal>.swiper-pagination-bullets, 
.swiper-pagination-bullets.swiper-pagination-horizontal {
    @media (min-width: $desktop-width) {
        bottom: 66px;
    }
}

.swiper-pagination-bullet {
    display: none;

    @media (min-width: $desktop-width) {
        display: block;
        width: 62px;
        height: 4px;
        border-radius: 0%;
        background-color: ffffff;
    }
}

.swiper-pagination-bullet-active {
    @media (min-width: $desktop-width) {
        background-color: #7859cf;
    }
}