.pagination {
    @media (min-width: $desktop-width) {
        grid-column: 1 / -1;
        grid-row: 5 / 6;
        justify-self: end;
    }
}

.pagination__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style-type: none;

    @media (min-width: $tablet-width) {
        justify-content: flex-end;
    }
}

.pagination__item {
    min-width: 40px;
    min-height: 40px;
    font-size: 16px;
    line-height: 24px;
    margin-right: 20px;
}

.pagination__item:first-child {
    @media (min-width: $tablet-width) {
        min-width: 135px;
        margin-right: -13px;
    }
}

.pagination__item:last-child { 
    margin-right: 0;

    @media (min-width: $tablet-width) {
        min-width: 135px;
        margin-left: -30px;
    }
}

.pagination__link {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 40px;
    min-height: 40px;
    color: #7859cf;
    background-color: #ffffff;
    border-radius: 4px;
    border: none;

    &:hover,
    &:focus {
        outline: none;
        color: #ffffff;
        background-color: #9070ec;
    }

    &:active {
        background-color: #593cac;
    }

    &:disabled {
        background-color: #e2e2e2;
    }

    @media (min-width: $tablet-width) {
        font-size: 16px;
        line-height: 24px;
    }
}

.pagination__current {
    color: #333333;
    background-color: transparent;
    font-weight: 600;
}

.pagination__text {
    display: none;

    @media (min-width: $tablet-width) {
        display: block;
    }
}

.pagination__link svg {
    @media (min-width: $tablet-width) {
        display: none;
    } 
}

.pagination__prev,
.pagination__next {
    width: 100%;
}

.pagination__prev svg,
.pagination__next svg {
    fill: #7859cf;
}

.pagination__link:hover.pagination__prev svg,
.pagination__link:hover.pagination__next svg {
    fill: #ffffff;
}

.pagination__link:focus.pagination__prev svg,
.pagination__link:focus.pagination__next svg {
    fill: #ffffff;
}
