.range {
    @media (min-width: $tablet-width) {
        margin-left: 8px;
    }

    @media (min-width: $desktop-width) {
        margin-left: 0;
    }
}

.range__scale {
    position: relative;
    height: 24px;
    margin-bottom: 15px;

    @media (min-width: $tablet-width) {
        margin-bottom: 12px;
    }
}

.range__toggle {
    position: absolute;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    border: 2px solid #9070ec;
    z-index: 2;
    cursor: pointer;

    &:hover,
    &:focus {
        outline: transparent;
        &::after {
            position: absolute;
            content: "";
            width: 16px;
            height: 16px;
            border-radius: 50%;
            background-color: rgba(120, 89, 207, 0.25);
            top: 2px;
            left: 2px;
        }
    }

    &:active {
        &::after {
            background-color: #7859cf;
        }
    }

    &:disabled {
        border-color: #bdbdbd;
        background-color: #f8f8f8;
    }
}

.range__bar {
    margin: 0;
    padding: 0;
    border: none;
    vertical-align: top;
    width: 280px;
    height: 4px;
    background: linear-gradient(to right, #9070ec 242px, #e2e2e2 242px);

    &:disabled {
        background: linear-gradient(to right, #bdbdbd, #bdbdbd);
    }

    @media (min-width: $tablet-width) {
        width: 220px;
        background: linear-gradient(to right, #9070ec 190px, #e2e2e2 190px);
    }

    @media (min-width: $desktop-width) {
        width: 318px;
        background: linear-gradient(to right, #9070ec 274px, #e2e2e2 274px);
    }
}

.range__toggle-min {
    top: -50%;
    left: 0;
    transform: translateY(2px);
}

.range__toggle-max {
    top: -50%;
    right: 20px;
    transform: translateY(2px);

    @media (min-width: $tablet-width) {
        right: 20px;
    }
}

.range__wrapper-inputs {
    position: relative;
    display: flex;
    justify-content: space-between;

    &::after {
        position: absolute;
        content: "";
        width: 10px;
        height: 2px;
        background-color: #bdbdbd;
        top: 50%;
        right: 50%;
        transform: translate(5px, 1px);
    }
}

.range__input {
    width: 118px;
    height: 38px;
    border: 2px solid #9070ec;
    border-radius: 4px;

    @media (min-width: $tablet-width) {
        width: 93px;
        height: 40px;
    }
    
    @media (min-width: $desktop-width) {
        width: 134px;
    }

    &:focus {
        color: #333333;
        outline-color: #593cac;
    }

    &:disabled {
        border-color: #e2e2e2;
    }
}

.range__input-min,
.range__input-max {
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    text-align: center;
}

.range__input-min {
    color: #bdbdbd;
}

