.navigation-user {
    margin-left: auto;
    
    @media (min-width: $tablet-width) {
        order: 3;
        margin-left: 40px;
    }
}

.navigation-user__wrapper {
    display: flex;
    justify-content: space-between;
    min-width: 120px;
    align-items: center;
}

.navigation-user__entry,
.navigation-user__basket {
    padding: 22px;
    min-width: 60px;
    text-decoration: none;
    color: #333333;

    &:hover,
    &:focus {
        color: #7859cf;
    }

    &:active {
        background-color: #f0ebff;
        color: #333333
    }

    @media (min-width: $desktop-width) {
        display: flex;

        &:active {
            background-color: #f0ebff;
            color: #7859cf;
        }
    }
}

.navigation-user__entry {
    @media (min-width: $desktop-width) {
        margin-right: 20px;
    }
}

.navigation-user__entry-text,
.navigation-user__basket-text {
    display: none;

    @media (min-width: $desktop-width) {
        display: block;
        margin-left: 8px;
    }
}