.catalog__button {
  position: relative;
  padding: 8px 24px 10px 45px;
  font-family: inherit;
}
.catalog__button::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 16px;
  top: 50%;
  transform: translateY(-8px);
  left: 16px;
  background-image: url("../images/button-icon.svg");
}

.controls__milk-label,
.controls__country-label {
  font-size: 16px;
  line-height: 24px;
}

.controls__milk-input,
.controls__country-input {
  display: none;
}

.controls__milk-mark {
  position: relative;
  width: 24px;
  height: 24px;
  margin-right: 10px;
}

.controls__milk-mark::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 24px;
  right: 0;
  border-radius: 50%;
  border: 2px solid #bdbdbd;
}

.controls__milk-input:checked + .controls__milk-mark::before {
  border-color: #7859cf;
}
.controls__milk-input:checked + .controls__milk-mark::after {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  right: 0;
  top: 1px;
  transform: translateX(-4px);
  border-radius: 50%;
  background-color: #7859cf;
}

.controls__milk-input:hover + .controls__milk-mark::before,
.controls__milk-input:focus + .controls__milk-mark::before {
  border-color: #7859cf;
}
.controls__milk-input:hover + .controls__milk-mark::after,
.controls__milk-input:focus + .controls__milk-mark::after {
  background-color: rgba(120, 89, 207, 0.25);
}

.controls__milk-input:disabled ~ .controls__milk-mark::before {
  border-color: #e2e2e2;
}

.controls__milk-input:disabled:checked ~ .controls__milk-mark::after {
  background-color: #e2e2e2;
}

.controls__country-mark {
  position: relative;
  width: 42px;
  height: 24px;
  margin-right: 10px;
}

.controls__country-mark::before {
  position: absolute;
  content: "";
  right: 0;
  width: 42px;
  height: 24px;
  border-radius: 12px;
  background-color: #bdbdbd;
}
.controls__country-mark::after {
  position: absolute;
  content: "";
  right: 20px;
  top: -1px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #ffffff;
}

.controls__country-input:checked + .controls__country-mark::before {
  background-color: #7859cf;
}
.controls__country-input:checked + .controls__country-mark::after {
  right: 2px;
}

.controls__country-input:hover + .controls__country-mark::before,
.controls__country-input:focus + .controls__country-mark::before {
  border: 2px solid #9070ec;
}

.controls__country-input:disabled ~ .controls__country-mark::before {
  background-color: #e2e2e2;
}

.controls__country-input:disabled:checked ~ .controls__country-mark::after {
  background-color: #bdbdbd;
  right: 2px;
}

