.advantages__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 0 18px;
    margin-bottom: 30px;

    @media (min-width: $tablet-width) {
        padding-top: 55px;
    }

    @media (min-width: $desktop-width) {
        margin-bottom: 52px;
    }
}

.advantages__header {
    margin-bottom: 5px;
}

.advantages__reasons {
    margin: 0;
    margin-bottom: 15px;
    padding: 0;
    font-size: 24px;
    line-height: 36px;
    font-weight: 700;
    text-align: center;

    @media (min-width: $tablet-width) {
        font-size: 42px;
        line-height: 64px;
        margin-bottom: 60px;
    }
}

.advantages__list {
    display: grid;
    grid-template-columns: 280px;
    margin: 0;
    padding: 0;
    list-style-type: none;
    align-items: center;

    @media (min-width: $tablet-width) {
        grid-template-columns: repeat(2, 365px);
        margin: 0 30px;
    }

    @media (min-width: $desktop-width) {
        grid-template-columns: repeat(4, 323px);
        column-gap: 25px;
    }
}

.advantages__item {
    position: relative;
    text-align: center;
    margin-bottom: 10px;
    padding-top: 87px;

    @media (min-width: $tablet-width) {
        width: 349px;
        text-align: start;
        padding: 0;
        margin: 0;
        margin-bottom: 50px;
    }

    @media (min-width: $desktop-width) {
        text-align: center;
        max-width: 332px;
    }
}

.advantages__item--speed,
.advantages__item--convenience,
.advantages__item--cheerfulness,
.advantages__item--ecology {
    &::before {
        position: absolute;
        content: "";
        width: 50px;
        height: 56px;
        top: 20px;
        left: 50%;
        transform: translateX(-25px);

        @media (min-width: $tablet-width) {
            top: 5%;
            left: 11px;
            transform: translateX(0);
        }
        
        @media (min-width: $desktop-width) {
            top: 20px;
            left: 50%;
            transform: translateX(-25px);
        }
    }
}

.advantages__item--speed {
    &::before {
        background-image: url("../images/speed.svg");
    }
}

.advantages__item--convenience {
    &::before {
        background-image: url("../images/convenience.svg");
    }
}

.advantages__item--cheerfulness {
    &::before {
        width: 42px;
        background-image: url("../images/cheerfulness.svg");
    }
}

.advantages__item--ecology {
    &::before {
        width: 42px;
        background-image: url("../images/ecology.svg");
    }
}

.advantages__item-header {
    margin: 0;
    margin-bottom: 5px;
    padding: 0;
    font-size: 18px;
    line-height: 32px;
    font-weight: 700;

    @media (min-width: $tablet-width) {
        font-size: 24px;
        line-height: 36px;
        padding-left: 80px;
        margin-bottom: 10px;
    }

    @media (min-width: $desktop-width) {
        padding-top: 95px;
        padding-left: 0;
    }
}

.advantages__item-description {
    margin: 0;
    padding: 0;

    @media (min-width: $tablet-width) {
        padding-left: 80px;
        font-size: 16px;
        line-height: 24px;
    }

    @media (min-width: $desktop-width) {
        padding-left: 0;
    }
}

.advantages__item--convenience .advantages__item-header,
.advantages__item--convenience .advantages__item-description,
.advantages__item--ecology .advantages__item-header,
.advantages__item--ecology .advantages__item-description  {
    @media (min-width: $tablet-width) {
        padding-left: 90px;
    }

    @media (min-width: $desktop-width) {
        padding-left: 0;
    }

}