.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
}
  
.content-center {
    width: 280px;
    margin: 0 auto;
  
    @media (min-width: $tablet-width) {
        width: 728px;
    }
  
    @media (min-width: $desktop-width) {
        width: 1360px;
    }
}
  
img {
    max-width: 100%;
    object-fit: contain;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    width: 100%;
    min-width: 320px;
    max-width: 100%;
    overflow-x: hidden;
}

body {
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;
    margin: 0;
    font-family: $basic-font;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #333333;
}

.button {
    font-family: inherit;
    background-color: #7859cf;
    border: none;
    border-radius: 4px;
    color: #ffffff;
    cursor: pointer;

    &:hover,
    &:focus {
        outline: none;
        background-color: #9070ec;
    }

    &:active {
        background-color: #593cac;
    }

    &:disabled {
        background-color: #bdbdbd;
    }
}

.title {
    margin: 0;
    padding: 0;
    font-size: 12px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 0.4px;
    text-transform: uppercase;
    color: #7859cf;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none; 
}

input[type='number'],
input[type="number"]:hover,
input[type="number"]:focus {
    appearance: none;
    -moz-appearance: textfield;
}